const grid_1 = require("./ui/grid");
const popupnumbers_1 = require("./ui/popupnumbers");
const grid = new grid_1.default($('#container'));
grid.build();
grid.layout();
const popupnumber = new popupnumbers_1.default($('#popupNumbers'));
grid.bindPopup(popupnumber);
$('#check').on('click', e => {
    if (grid.check()) {
        alert('Congratulations！');
    }
    ;
});
$('#reset').on('click', e => {
    grid.reset();
});
$('#clear').on('click', e => {
    grid.clear();
});
$('#rebuild').on('click', e => {
    grid.reBuild();
});



